import PropTypes from 'prop-types';

// @mui
import { useTheme, styled } from '@mui/material/styles';
import {
  Avatar,
  Box,
  Card,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Typography
} from '@mui/material';

// project
import TotalIncomeCard from 'components/cards/Skeleton/TotalIncomeCard';
import { InfoRounded } from '@mui/icons-material';

// styles
const CardWrapper = styled(Card)(({ theme, color }) => ({
  backgroundColor: theme.palette[color]?.['800'],
  color: theme.palette.common.white,
  overflow: 'hidden',
  position: 'relative',
  '&:after': {
    content: '""',
    position: 'absolute',
    width: 210,
    height: 210,
    background: `linear-gradient(210.04deg, ${theme.palette[color]?.light} -50.94%, rgba(144, 202, 249, 0) 83.49%)`,
    borderRadius: '50%',
    top: -30,
    right: -180
  },
  '&:before': {
    content: '""',
    position: 'absolute',
    width: 210,
    height: 210,
    background: `linear-gradient(140.9deg, ${theme.palette[color].light} -14.02%, rgba(144, 202, 249, 0) 70.50%)`,
    borderRadius: '50%',
    top: -160,
    right: -130
  }
}));

// -------------------------------------------------------------------------------

const InfoCard = ({
  isLoading,
  caption = 'Caption',
  value = '0.00',
  icon = <InfoRounded />,
  color = 'primary'
}) => {
  const theme = useTheme();
  return isLoading ? (
    <TotalIncomeCard />
  ) : (
    <CardWrapper border={false} content={false} color={color}>
      <Box sx={{ p: 2 }}>
        <List sx={{ py: 0 }}>
          <ListItem alignItems="center" disableGutters sx={{ py: 0 }}>
            <ListItemAvatar>
              <Avatar
                variant="rounded"
                sx={{
                  ...theme.typography.commonAvatar,
                  ...theme.typography.largeAvatar,
                  backgroundColor: theme.palette[color]?.dark,
                  color: theme.palette.common.white
                }}
              >
                {icon}
              </Avatar>
            </ListItemAvatar>
            <ListItemText
              sx={{
                py: 0,
                mt: 0.45,
                mb: 0.45
              }}
              primary={
                <Typography
                  variant="h4"
                  sx={{
                    color: theme.palette.common.white
                  }}
                >
                  {value}
                </Typography>
              }
              secondary={
                <Typography
                  variant="subtitle2"
                  sx={{
                    color: theme.palette.common.white,
                    mt: 0.5
                  }}
                >
                  {caption}
                </Typography>
              }
            />
          </ListItem>
        </List>
      </Box>
    </CardWrapper>
  );
};

InfoCard.propTypes = {
  isLoading: PropTypes.bool
};

export default InfoCard;
